html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  background-color: #000;
  color: #d6d8d9;
  font-family: "Lato", sans-serif;
  margin-top: 1em;
}

body a:link {
  color: #62bb46;
}

body a:hover, body a:focus {
  color: #fff200;
}

body a:visited {
  color: #3b712a;
}

a {
  transition: all 150ms linear;
  text-decoration: none;
}

p {
  font-size: 18px;
  line-height: 1.6;
}

img {
  max-width: 100%;
}

.masthead {
  width: 100%;
  text-transform: uppercase;
  font-weight: bold;
  padding: 0 1em;
}

.masthead a {
  margin: 0 1em;
}

.masthead a:link, .masthead a:visited {
  display: inline-block;
  vertical-align: middle;
  color: #bcbec0;
  font-size: 36px;
}

.masthead a:hover {
  color: #62bb46;
  border-color: transparent;
}

.masthead .selected {
  border-bottom: 3px solid #62bb46;
}

.masthead .logo {
  width: 20%;
  text-align: center;
  margin: 0;
}

.masthead .logo:hover {
  opacity: .7;
}

.masthead .links {
  width: 40%;
  display: inline-block;
  text-align: center;
}

.main {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.main::after {
  clear: both;
  content: "";
  display: block;
}

footer {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.7);
  padding-top: 9px;
  background-color: #303233;
}

footer h6 {
  margin: 9px 0;
}

footer a {
  max-width: 150px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 18px;
  margin-bottom: 18px;
}

footer a:link, footer a:visited {
  color: #f1f1f1;
  opacity: .7;
}

footer a:hover, footer a:focus, footer a:active {
  opacity: 1;
  color: #62bb46;
}

footer small {
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 18px;
  padding: 9px 0;
  background-color: #0b0c0c;
  color: #bcbec0;
}

.footerMeta {
  display: inline-block;
  width: 33%;
  min-height: 139px;
  vertical-align: top;
  color: #f1f1f1;
}

.footerLinks {
  list-style: none;
  float: left;
  width: 50%;
}

[class^="icon-"], [class*=" icon-"] {
  font-size: 24px;
}
